<template>
  <div id="app">
    <div class="Contents" :class="{Success:editorCodeError === null,Error:editorCodeError !== null}">
      <textarea class="Editor" v-model="editorCode" autofocus></textarea>
      <div class="Error" v-if="editorCodeError">{{editorCodeError}}</div>
    </div>
  </div>
</template>

<script>

import Programmator from "./programmator.parser.js";

export default {
  name: "app",
  components: {},
  data() {
    return {
      editorCode: "",
      editorCodeError: null
    };
  },
  mounted() {
    if("PROGRAMMATOR_EDITOR_WEBPAGE" in localStorage) {
      this.editorCode = localStorage.PROGRAMMATOR_EDITOR_WEBPAGE;
    }
  },
  watch: {
    editorCode(newValue) {
      //console.log(require("./programmator.parser.js"));
      try {
        Programmator.parse(newValue);
        this.editorCodeError = null;
        localStorage.PROGRAMMATOR_EDITOR_WEBPAGE = newValue;
      } catch(error) {
        this.editorCodeError = error;
      }
    }
  }
};

</script>

<style>

body,html {
  margin: 0;
  padding: 0;
  background-color: #111;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  box-sizing: border-box;
  background-color: #111;
}
h1,h2,h3,h4,h5,h6 {
  margin: 0;
  padding: 0;
}

#app .Editor {
  display: inline-block;
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  max-width: 500px;
  height: 100%;
  min-height: 400px;
  
  background-color: #111;
  color: white;
  text-shadow: 0px 0px 3px white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  resize: vertical;
}
#app .Contents {}
#app .Contents.Success .Editor,
#app .Contents.Success .Editor:focus {
  border: 3px double #0f0;
}
#app .Contents.Error .Editor,
#app .Contents.Error .Editor:focus {
  border: 3px double red;
}
#app .Error {
  background: red;
  color: yellow;
  position: fixed;
  top: auto;
  bottom: 10px;
  left: 0;
  right: 0;
  text-align: center;
}

</style>
